import React, { Component } from 'react'
import styles from "./CalendarWidget.module.scss"

function calendarHeaderRow() {
    return (
        <div className={`${styles.calendarRow} ${styles.header}`}>
            <span className={styles.event}>Časť</span>
            <span className={styles.time}>Čas</span>
            <span className={styles.tv}>Vysiela</span>
        </div>
    )
}

function CalendarRow(props) {
    return (
        <div className={`${styles.calendarRow} ${props.event === "1. tréning" ? styles.noBorder : ""}`}>
            <span className={styles.event}>{props.event}</span>
            <span className={styles.time}>{props.time}</span>
            <span className={styles.tv}>{props.tv}</span>
        </div>
    )
}

class CalendarWidgetContent extends Component {
    render() {
        return (
            <div className={styles.content}>
                <div className={styles.venueBlock}>
                    <p className={styles.venueTitle}>VC Veľkej Británie</p>
                    <p className={styles.venueDate}>22. - 24. marec 2020</p>
                </div>
                {calendarHeaderRow()}
                <CalendarRow event="1. tréning" time="06:00 - 07:30" tv="Sport2 od 05:55"/>
                <CalendarRow event="2. tréning" time="06:00 - 07:30" tv="Sport2 od 05:55"/>
                <CalendarRow event="3. tréning" time="06:00 - 07:30" tv="Sport2 od 05:55"/>
                <CalendarRow event="kvalifikácia" time="06:00 - 07:30" tv="Sport2 od 05:55"/>
                <CalendarRow event="preteky" time="06:00 - 07:30" tv="Sport1 od 05:55"/>
            </div>
        )
    }
}

export default CalendarWidgetContent